import ReactDOM from 'react-dom';
import React, { Component } from 'react';
import * as firebase from 'firebase';
import './assets/css/bootstrap.min.css';
import $ from 'jquery';

class Form extends Component
{
   
constructor(props) {
    super(props);
   
 }

handleLoad()
{
    console.log('record inserted');
    document.getElementById("form-data").innerHTML="worked";  
        
        
        
}
updateState()
{   
  var config = {
    apiKey: "AIzaSyARZMexghuwahAIiJ_Qrq5HjM-xExlKIyw",
    authDomain: "emp-manegment.firebaseapp.com",
    databaseURL: "https://emp-manegment.firebaseio.com",
    projectId: "emp-manegment",
    storageBucket: "emp-manegment.appspot.com",
    messagingSenderId: "335360468884"
  };
firebase.initializeApp(config);

    
   var  username = document.getElementById('username').value;
   var  email    = document.getElementById('email').value;
   var phone    = document.getElementById('phone').value;            
    firebase.database().ref("emp").push().set({
                "username": username,
                "email": email,
                "phone":phone
            });
 var ref =firebase.database().ref('emp');
 
 ref.on('value',function(data)
 {
     data =data.val();
     var item='';
     $.each(data,function(i,value)
     {
      item = '<tr><td>'+value.username+"</td>";
      item +="<td>"+value.phone+"</td>";
      item +='<td>'+value.email+'</td>';
     $("#userlist").append(item);
     });
 });
 

        
            
}
    render() {
        return (
                <div className="col-md-12" >
                    <label>Username</label>
                    <input name="username"  id="username"  type="text" className="form-control" />
                    <label>Email</label>
                    <input name="email" id="email"  type="text" className="form-control" />
                    <label>Phone</label>
                    <input name="phone" id="phone"  type="text" className="form-control" />
                    <button className="btn btn-primary" onClick = {this.updateState}>CLICK</button>
                    <div id='form-data'  >
                    
                
                    </div>  
                </div>
               
                );
    }

}
 $(document).ready(function(e)
    {
        var config = {
    apiKey: "AIzaSyARZMexghuwahAIiJ_Qrq5HjM-xExlKIyw",
    authDomain: "emp-manegment.firebaseapp.com",
    databaseURL: "https://emp-manegment.firebaseio.com",
    projectId: "emp-manegment",
    storageBucket: "emp-manegment.appspot.com",
    messagingSenderId: "335360468884"
  };
firebase.initializeApp(config);
       var ref =firebase.database().ref('emp');
 
 ref.on('value',function(data)
 {
     data =data.val();
     var item='';
     $.each(data,function(i,value)
     {
      item = '<tr><td>'+value.username+"</td>";
      item +="<td>"+value.phone+"</td>";
      item +='<td>'+value.email+'</td>';
     $("#userlist").append(item);
     });
 }); 
    });
    

export default Form;




